.input {
	border: 0;
	background: var(--white);
	outline: none;
	width: 100%;
	caret-color: var(--black);
}

/* color */
.input.input-softBlue,
.input.input-softBlue::placeholder {
	color: var(--soft-blue);
}

.input.input-tomato,
.input.input-tomato::placeholder {
	color: var(--tomato);
}

.input.input-black,
.input.input-tomato::placeholder {
	color: var(--black);
}

.input.input-white,
.input.input-tomato::placeholder {
	color: var(--white);
}

/* font */
.input.input-h1 {
	font: var(--h1);
	height: 63px;
}

.input.input-h2 {
	font: var(--h2);
	height: 53px;
}

.input.input-h3 {
	font: var(--h3);
	height: 43px;
}

.input.input-body1 {
	font: var(--body1);
	height: 33px;
}

.input.input-body2 {
	font: var(--body2);
	height: 23px;
}

.input.input-body3 {
	font: var(--body3);
	height: 13px;
}
